import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            search: this.$route.query.name,
            id: this.$route.query.id,
            list: [],
            Um: [],
            from: this.$route.query.from,
            page: 1,
            size: 15,
            haveMore: true,
            user: this.$store.state.userInfo,
            // user: {
            //     Data: {
            //         User:{
            //             OrgId:1,
            //             DoctorId: 18
            //         }
            //     }
            // },
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        searchFor(){
            this.haveMore = true
            this.pull(false)
        },
        pull(h){
            if(this.haveMore){
                this.$post('Prescription/Medication/GetMedicationByNameForOftenUsed',this.$pick({
                    MedicationName: this.search,
                    OrgId: this.user.Data.User.OrgId,
                    DoctorId: this.user.Data.User.DoctorId,
                    MedicationClassId: this.id,
                    PageIndex: this.page,
                    PageSize: this.size
                })).then(o => {
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    if(this.from === 'up'){
                        const list = this.$store.state.upList
                        list.forEach(a => {
                            o.ReturnData.forEach(v => {
                                if(v.Name === a.Name && v.Brand === a.Brand){
                                    v.state = 'onUp'
                                }
                            })
                        })
                    }
                    else if(this.from === 'p'){
                        const list = this.$store.state.upList
                        list.forEach(a => {
                            o.ReturnData.forEach(v => {
                                if(v.Name === a.Name && v.Brand === a.Brand){
                                    v.state = 'onP'
                                }
                            })
                        })
                    }
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        toUm(i,idx){
            this.$post('Prescription/Medication/SaveMedicationOftenUsed',{
                MedicationId: i.Id,
                MedicationCompanyId: i.MedicationCompanyId,
                OrgId: this.user.Data.User.OrgId,
                DoctorId: this.user.Data.User.DoctorId
            }).then(o => {
                this.list[idx].IsAdd = true
            })
        },
        toUp(i){
            this.$store.dispatch('pushcMed', i)
            this.$router.push('/consumption?back=-4')
        },
        getMore(){
            const el = this.$refs.el
            let timer = null

            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        }
    }
}



